</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closeshenlunceshiCountType = true
		countTypeList.value.hiddenshenlunceshiCountType = true
		if(btnAuth('shenlunceshi','首页总数')){
			getshenlunceshiCount()
		}
		countTypeList.value.closeceshipingfenCountType = true
		countTypeList.value.hiddenceshipingfenCountType = true
		if(btnAuth('ceshipingfen','首页总数')){
			getceshipingfenCount()
		}
		countTypeList.value.closechengjifenxiCountType = true
		countTypeList.value.hiddenchengjifenxiCountType = true
		if(btnAuth('chengjifenxi','首页总数')){
			getchengjifenxiCount()
		}
	}
	const shenlunceshiCount = ref(0)
	const getshenlunceshiCount = () => {
		context?.$http({
			url:'shenlunceshi/count',
			method: 'get'
		}).then(res=>{
			shenlunceshiCount.value = res.data.data
		})
	}
	const ceshipingfenCount = ref(0)
	const getceshipingfenCount = () => {
		context?.$http({
			url:'ceshipingfen/count',
			method: 'get'
		}).then(res=>{
			ceshipingfenCount.value = res.data.data
		})
	}
	const chengjifenxiCount = ref(0)
	const getchengjifenxiCount = () => {
		context?.$http({
			url:'chengjifenxi/count',
			method: 'get'
		}).then(res=>{
			chengjifenxiCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closeshenlunceshiChartType1: true,
		hiddenshenlunceshiChartType1: true,
		closeceshipingfenChartType1: true,
		hiddenceshipingfenChartType1: true,
		closechengjifenxiChartType1: true,
		hiddenchengjifenxiChartType1: true,
		closechengjifenxiChartType2: true,
		hiddenchengjifenxiChartType2: true,
		closechengjifenxiChartType3: true,
		hiddenchengjifenxiChartType3: true,
	})
	const getCardList = () => {
		if(btnAuth('shenlunceshi','首页统计')){
			getshenlunceshiChart1()
		}
		if(btnAuth('ceshipingfen','首页统计')){
			getceshipingfenChart1()
		}
		if(btnAuth('chengjifenxi','首页统计')){
			getchengjifenxiChart1()
		}
		if(btnAuth('chengjifenxi','首页统计')){
			getchengjifenxiChart2()
		}
		if(btnAuth('chengjifenxi','首页统计')){
			getchengjifenxiChart3()
		}
	}
	const getshenlunceshiChart1 = () => {
		nextTick(()=>{
			var ceshileixingEchart1 = echarts.init(document.getElementById("shenlunceshiceshileixingEchart1"),'macarons');
			context?.$http({
				url: "shenlunceshi/group/ceshileixing",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].ceshileixing);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].ceshileixing
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '测试类型统计',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				ceshileixingEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				ceshileixingEchart1.setOption(option);
				//根据窗口的大小变动图表
				ceshileixingEchart1.resize();
			})
		})
	}
	const getceshipingfenChart1 = () => {
		nextTick(()=>{
			var pingfenEchart1 = echarts.init(document.getElementById("ceshipingfenpingfenEchart1"),'macarons');
			context?.$http({
				url: `ceshipingfen/value/yonghuzhanghao/pingfen`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].yonghuzhanghao);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].yonghuzhanghao
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '申论测试分析',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				pingfenEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				pingfenEchart1.setOption(option);
				//根据窗口的大小变动图表
				pingfenEchart1.resize();
			})
		})
	}
	const getchengjifenxiChart1 = () => {
		nextTick(()=>{
			var xuanzetizhanbiEchart1 = echarts.init(document.getElementById("chengjifenxixuanzetizhanbiEchart1"),'macarons');
			context?.$http({
				url: `chengjifenxi/value/kaoshimingcheng/xuanzetizhanbi`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].kaoshimingcheng);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].kaoshimingcheng
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '选择题统计分析',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				xuanzetizhanbiEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				xuanzetizhanbiEchart1.setOption(option);
				//根据窗口的大小变动图表
				xuanzetizhanbiEchart1.resize();
			})
		})
	}
	const getchengjifenxiChart2 = () => {
		nextTick(()=>{
			var tuilitizhanbiEchart2 = echarts.init(document.getElementById("chengjifenxituilitizhanbiEchart2"),'macarons');
			context?.$http({
				url: `chengjifenxi/value/kaoshimingcheng/tuilitizhanbi`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].kaoshimingcheng);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].kaoshimingcheng
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '推理题分析',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: ['25%', '55%'],
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				// 使用刚指定的配置项和数据显示图表。
				tuilitizhanbiEchart2.clear()
				tuilitizhanbiEchart2.setOption(option);
				//根据窗口的大小变动图表
				tuilitizhanbiEchart2.resize();
			})
		})
	}
	const getchengjifenxiChart3 = () => {
		nextTick(()=>{
        //  dazuowenzhanbi
			var dazuowenzhanbiEchart3 = echarts.init(document.getElementById("chengjifenxidazuowenzhanbiEchart3"),'macarons');
			context?.$http({
				url: `chengjifenxi/value/kaoshimingcheng/dazuowenzhanbi`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].kaoshimingcheng);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].kaoshimingcheng
				    })
				}
				var option = {};
				option = {
				    title: {
				        text: '大作文分析',
				        left: 'center'
				    },
				    tooltip: {
				      trigger: 'item',
				      formatter: '{b} : {c}'
				    },
				    xAxis: {
				        type: 'category',
				        boundaryGap: false,
				        data: xAxis
				    },
				    yAxis: {
				        type: 'value'
				    },
				    series: [{
				        data: yAxis,
				        type: 'line',
						areaStyle: {},
						smooth: true
                    }]
                };
				// 使用刚指定的配置项和数据显示图表。
				dazuowenzhanbiEchart3.clear()
				dazuowenzhanbiEchart3.setOption(option);
				//根据窗口的大小变动图表
				dazuowenzhanbiEchart3.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 0 0 20px;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 30px;
		justify-content: center;
		align-items: center;
		height: 50%;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 1px solid #e4e7ed;
			box-shadow: 0px 0px 12px rgba(0,0,0,.12);
			padding: 0 0 50px;
			margin: 0 10px 10px;
			flex: 1;
			background: rgba(0,123,213,1);
			width: 100%;
			position: relative;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border: 1px solid #e4e7ed;
				padding: 10px;
				left: 0;
				bottom: 0;
				width: 100%;
				border-width: 1px 0 0;
				position: absolute;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #fff;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #fff;
